"use client";

import { useState } from "react";
import { Loader2 } from "lucide-react";

export default function LearningFeedPage() {
  const [industry, setIndustry] = useState("");
  const [skills, setSkills] = useState("");
  const [interests, setInterests] = useState("");
  const [feed, setFeed] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleGenerateFeed = async () => {
    setLoading(true);
    try {
      const res = await fetch("/main/learning-feed", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          industry,
          skills: skills.split(",").map(s => s.trim()),
          interests: interests.split(",").map(i => i.trim()),
        }),
      });

      const data = await res.json();
      if (data.feed) setFeed(data.feed);
    } catch (err) {
      console.error("Failed to generate feed:", err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container mx-auto p-6">
      <h1 className="text-3xl font-bold mb-6">ðŸ“š Learning Feed</h1>

      {/* Input Form */}
      <div className="space-y-4 mb-6">
        <input
          type="text"
          placeholder="Your Industry"
          className="w-full border rounded-lg p-2"
          value={industry}
          onChange={(e) => setIndustry(e.target.value)}
        />
        <input
          type="text"
          placeholder="Skills (comma separated)"
          className="w-full border rounded-lg p-2"
          value={skills}
          onChange={(e) => setSkills(e.target.value)}
        />
        <input
          type="text"
          placeholder="Interests (comma separated)"
          className="w-full border rounded-lg p-2"
          value={interests}
          onChange={(e) => setInterests(e.target.value)}
        />
        <button
          onClick={handleGenerateFeed}
          className="bg-blue-600 text-white px-4 py-2 rounded-lg flex items-center"
          disabled={loading}
        >
          {loading && <Loader2 className="mr-2 h-5 w-5 animate-spin" />}
          {loading ? "Generating..." : "Generate Feed"}
        </button>
      </div>

      {/* Feed Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {feed.map((item, index) => (
          <div key={index} className="border rounded-lg p-4 bg-muted/20 shadow hover:shadow-lg transition">
            <h2 className="font-semibold text-lg mb-1">{item.title}</h2>
            <p className="text-sm text-gray-500 mb-2">
              {item.type} â€¢ {item.source} â€¢ {item.time}
            </p>

            <ul className="list-disc list-inside mb-2">
              {item.summary.map((bullet, i) => (
                <li key={i}>{bullet}</li>
              ))}
            </ul>

            <p className="text-sm font-medium text-blue-700">
              Relevance: {item.relevance}
            </p>
          </div>
        ))}
      </div>
    </div>
  );
}
